  align-items: flex-start;
  margin-right: 1rem;
  padding: .1rem;
  display: flex;
}

.new-calendar-controls .mode-radio div {
  justify-content: center;
  align-items: center;
  display: flex;
}

.new-calendar-controls .mode-radio label {
  cursor: pointer;
  margin-top: .1rem;
  margin-left: .5rem;
}

.new-calendar-controls select {
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: inherit;
  cursor: pointer;
  margin-right: 1rem;
  padding: .5rem;
}

.new-calendar-controls select option {
  font-family: inherit;
  font-weight: inherit;
}

.new-calendar-controls button {
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: inherit;
  cursor: pointer;
  border: none;
  padding: .5rem;
}

.new-calendar-controls button:focus {
  outline: none;
}

.new-calendar-controls button:hover {
  transform: scale(.95);
}

.new-calendar-controls button:active {
  transform: scale(.85);
}

.calendar-container {
  width: 100%;
  height: calc(100% - 120px);
  position: absolute;
  top: 60px;
}

.calendar-wrapper {
  width: 100%;
  height: 100%;
  align-items: flex-start;
  display: flex;
  position: relative;
  overflow: hidden;
}

.calendar-inner {
  height: 100%;
  align-items: center;
  transition: all .1s ease-in;
  display: flex;
  position: absolute;
}

.month-container, #mockup-container {
  width: 100vw;
  max-width: 100%;
  height: 95%;
  justify-content: center;
  align-items: center;
  padding: .5rem;
  display: flex;
  position: relative;
}

svg {
  height: 100%;
  filter: drop-shadow(3px 3px 12px #00000054);
}

svg > svg {
  filter: initial;
}

.loading-screen {
  width: 100%;
  height: calc(100% - 120px);
  z-index: 4;
  pointer-events: none;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 60px;
  left: 0;
}

.loading-screen svg {
  filter: initial;
}

.loading-screen.hide {
  display: none;
}

.cropper-outer-container {
  width: 100%;
  height: 100%;
  margin: 0;
  position: relative;
}

.image-element {
  max-height: 100%;
  max-width: 100%;
  margin: 0 auto;
  display: block;
}

.controls-container {
  height: 60px;
  z-index: 2;
  width: 100%;
  background-color: #231f20;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  bottom: 0;
}

.controls-container button, .controls-container label {
